      <!-- Ê£ÄÊµãÂà∞ÁöÑÂä®‰Ωú -->
      <se-card title="Ê£ÄÊµãÂà∞ÁöÑÂä®‰Ωú" :subtitle="`ÂÖ± ${detectedActions.length} ‰∏™`" class="mt-4">
        <view class="actions-list">
          <view
            v-for="action in detectedActions"
