                <text class="action-time">{{ action.timeStart }} - {{ action.timeEnd }}</text>
              </view>
            </view>

            <view class="action-right">
              <text class="action-score">{{ action.score }}ÂàÜ</text>
              <view class="quality-bars">
                <view
                  v-for="bar in 5"
                  :key="bar"
                  class="quality-bar"
                  :class="{ 'quality-bar--filled': bar <= Math.ceil(action.score / 20) }"
                ></view>
              </view>
            </view>
          </view>
        </view>
      </se-card>

      <!-- AI ÊïôÁªÉÂèçÈ¶à -->
      <se-card title="AI ÊïôÁªÉÂèçÈ¶à" variant="gradient" class="mt-4">
        <view class="coach-feedback">
          <view class="coach-avatar">
            <text class="coach-icon">ü§ñ</text>
          </view>
          <view class="feedback-content">
            <text class="feedback-text">{{ coachFeedback }}</text>
          </view>
        </view>

        <view class="suggestions">
          <text class="suggestions-title">ÊîπËøõÂª∫ËÆÆ</text>
          <view class="suggestion-item" v-for="(suggestion, index) in suggestions" :key="index">
            <text class="suggestion-bullet">‚Ä¢</text>
            <text class="suggestion-text">{{ suggestion }}</text>
          </view>
        </view>
