          @click="shareResults"
        />
        <se-button
          type="primary"
          icon="download"
          text="‰∏ãËΩΩÊä•Âëä"
          @click="downloadReport"
        />
      </view>

      <!-- ÈáçÊñ∞ÂàÜÊûêÊåâÈíÆ -->
      <view class="reanalyze-section">
        <se-button
          type="primary"
