<script setup>
import { ref, computed } from 'vue'
import { analysisAPI, coachAPI } from '@/utils/api.js'
import { getCircleProgressStyle, getStarCount, getScoreComment, getMetricType, formatFileSize } from '@/utils/common.js'
import SeCard from '@/components/se-card/se-card.vue'
import SeButton from '@/components/se-button/se-button.vue'
import SeProgress from '@/components/se-progress/se-progress.vue'

// ‰∏ä‰º†Áä∂ÊÄÅ
const selectedFile = ref(null)
const uploading = ref(false)
const uploadProgress = ref(0)
const uploadStatusText = ref('ÂáÜÂ§á‰∏ä‰º†...')
const uploadSpeed = ref('')
const isDragging = ref(false)

// ÂàÜÊûêÁä∂ÊÄÅ
const analyzing = ref(false)
const analysisCompleted = ref(false)
const currentStep = ref(0)
const analysisSteps = ref([
  '‰∏ä‰º†ËßÜÈ¢ë‰∏≠...',
  'ÊèêÂèñÂÖ≥ÈîÆÂ∏ß...',
  'ÂßøÊÄÅËØÜÂà´‰∏≠...',
  'Âä®‰ΩúÂàÜÊûê‰∏≠...',
  'ÁîüÊàêÊä•Âëä‰∏≠...'
])

// ÂàÜÊûêÁªìÊûú
const sessionId = ref('')
const videoUrl = ref('')
const analysisDate = ref('')
const overallScore = ref(0)
const detailedMetrics = ref([])
const detectedActions = ref([])
const coachFeedback = ref('')
const suggestions = ref([])

// ÂéÜÂè≤ËÆ∞ÂΩï
const recentAnalysis = ref([
  { id: 1, title: 'Áõ¥Âà∫ËÆ≠ÁªÉ', date: '2025-12-17 14:30', score: 92 },
  { id: 2, title: 'Ê≠•Ê≥ïÁªÉ‰π†', date: '2025-12-16 10:15', score: 85 },
  { id: 3, title: 'ÂßøÊÄÅÁü´Ê≠£', date: '2025-12-15 16:00', score: 78 }
])

// ÈÄâÊã©Êñá‰ª∂
const selectFile = () => {
  uni.chooseVideo({
    sourceType: ['album', 'camera'],
    maxDuration: 600, // ÊúÄÂ§ß10ÂàÜÈíü
    camera: 'back',
    success: (res) => {
      selectedFile.value = {
        path: res.tempFilePath,
        name: 'ËÆ≠ÁªÉËßÜÈ¢ë.mp4',
        size: res.size,
        duration: res.duration
      }
      startUpload()
    },
    fail: (error) => {
      console.error('ÈÄâÊã©ËßÜÈ¢ëÂ§±Ë¥•:', error)
      uni.showToast({
        title: 'ÈÄâÊã©Êñá‰ª∂Â§±Ë¥•',
        icon: 'none'
      })
    }
  })
}

// ÂºÄÂßã‰∏ä‰º†
const startUpload = async () => {
  uploading.value = true
  uploadProgress.value = 0

  try {
    const startTime = Date.now()
    let lastProgress = 0

    // Ë∞ÉÁî®‰∏ä‰º† API
    const result = await analysisAPI.startAnalysis(
      selectedFile.value.path,
      {},
      (progress) => {
        uploadProgress.value = progress

        // ËÆ°ÁÆó‰∏ä‰º†ÈÄüÂ∫¶
        const elapsed = (Date.now() - startTime) / 1000 // Áßí
        const uploaded = (selectedFile.value.size * progress) / 100
        const speed = uploaded / elapsed / 1024 / 1024 // MB/s
        uploadSpeed.value = `${speed.toFixed(2)} MB/s`

        // Êõ¥Êñ∞Áä∂ÊÄÅÊñáÊú¨
        if (progress < 100) {
          uploadStatusText.value = `Ê≠£Âú®‰∏ä‰º†... ${progress.toFixed(1)}%`
        } else {
          uploadStatusText.value = '‰∏ä‰º†ÂÆåÊàêÔºåÂºÄÂßãÂàÜÊûê...'
        }

        lastProgress = progress
      }
    )

    // ‰∏ä‰º†ÂÆåÊàêÔºåÂºÄÂßãÂàÜÊûê
    sessionId.value = result.session_id
    uploading.value = false
    startAnalysis()

  } catch (error) {
    console.error('‰∏ä‰º†Â§±Ë¥•:', error)
    uploading.value = false
    uni.showToast({
      title: '‰∏ä‰º†Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
      icon: 'none'
    })
  }
}

// ÂºÄÂßãÂàÜÊûê
const startAnalysis = async () => {
  analyzing.value = true
  currentStep.value = 0

  try {
    // ËΩÆËØ¢ÂàÜÊûêÁä∂ÊÄÅ
    const pollInterval = setInterval(async () => {
      const status = await analysisAPI.getAnalysisStatus(sessionId.value)

      // Êõ¥Êñ∞Ê≠•È™§
      if (status.progress) {
        currentStep.value = Math.floor((status.progress / 100) * analysisSteps.value.length)
      }

      if (status.status === 'COMPLETED') {
        clearInterval(pollInterval)
        analyzing.value = false
        showResults(status.result)
      } else if (status.status === 'FAILED') {
        clearInterval(pollInterval)
        analyzing.value = false
        uni.showToast({
          title: 'ÂàÜÊûêÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      }
    }, 3000)

  } catch (error) {
    console.error('ÂàÜÊûêÂ§±Ë¥•:', error)
    analyzing.value = false
    uni.showToast({
      title: 'ÂàÜÊûêÂ§±Ë¥•',
      icon: 'none'
    })
  }
}

// ÊòæÁ§∫ÂàÜÊûêÁªìÊûú
const showResults = async (result) => {
  // ËÆæÁΩÆÂü∫Á°Ä‰ø°ÊÅØ
  videoUrl.value = result.video_url || selectedFile.value.path
  analysisDate.value = new Date().toLocaleString('zh-CN')
  overallScore.value = result.overall_score || 85

  // ËØ¶ÁªÜÊåáÊ†á
  detailedMetrics.value = [
    { id: 1, name: 'ÂßøÊÄÅÊ†áÂáÜÂ∫¶', score: result.posture_score || 88 },
    { id: 2, name: 'Âä®‰ΩúÊµÅÁïÖÂ∫¶', score: result.fluency_score || 82 },
    { id: 3, name: 'ÈÄüÂ∫¶ÊéßÂà∂', score: result.speed_score || 86 },
    { id: 4, name: 'ÂäõÈáèËæìÂá∫', score: result.power_score || 84 },
    { id: 5, name: 'Á≤æÂáÜÂ∫¶', score: result.accuracy_score || 90 }
  ]

  // Ê£ÄÊµãÂà∞ÁöÑÂä®‰Ωú
  detectedActions.value = result.actions || [
    {
      id: 1,
      name: 'Áõ¥Âà∫',
      icon: '‚Üí',
      color: 'rgba(59, 130, 246, 0.2)',
      timeStart: '00:12',
      timeEnd: '00:18',
      score: 92
    },
    {
      id: 2,
      name: 'Èò≤ÂÆà',
      icon: 'üõ°Ô∏è',
      color: 'rgba(16, 185, 129, 0.2)',
      timeStart: '00:25',
      timeEnd: '00:32',
      score: 85
    },
    {
      id: 3,
      name: 'ÂèçÂáª',
      icon: '‚ö°',
      color: 'rgba(245, 158, 11, 0.2)',
      timeStart: '00:40',
      timeEnd: '00:48',
      score: 88
    }
  ]

  // Ëé∑Âèñ AI ÊïôÁªÉÂèçÈ¶à
  try {
    const feedback = await coachAPI.getFeedback({
      type: 'fencing',
      score: overallScore.value,
      metrics: {
        posture: detailedMetrics.value[0].score,
        fluency: detailedMetrics.value[1].score,
        speed: detailedMetrics.value[2].score
      }
    })

    coachFeedback.value = feedback.feedback || 'ÊÇ®ÁöÑË°®Áé∞ÈùûÂ∏∏Âá∫Ëâ≤ÔºÅÁªßÁª≠‰øùÊåÅËøôÊ†∑ÁöÑËÆ≠ÁªÉÂº∫Â∫¶„ÄÇ'
    suggestions.value = feedback.suggestions || [
      'Ê≥®ÊÑè‰øùÊåÅÊ≠•Ê≥ïÁöÑÁ®≥ÂÆöÊÄßÔºåÈÅøÂÖçÈáçÂøÉËøáÂ∫¶ÂâçÂÄæ',
      'Âá∫ÂâëÊó∂ÊâãËÖïÂäõÂ∫¶ÂèØ‰ª•Êõ¥Âä†ÈõÜ‰∏≠',
      'Âª∫ËÆÆÂ¢ûÂä†ÊüîÈüßÊÄßËÆ≠ÁªÉÔºåÊèêÈ´òÂä®‰ΩúÂπÖÂ∫¶'
    ]
  } catch (error) {
    console.error('Ëé∑ÂèñÂèçÈ¶àÂ§±Ë¥•:', error)
    coachFeedback.value = 'ÊÇ®ÁöÑË°®Áé∞ÈùûÂ∏∏Âá∫Ëâ≤ÔºÅÁªßÁª≠‰øùÊåÅËøôÊ†∑ÁöÑËÆ≠ÁªÉÂº∫Â∫¶„ÄÇ'
    suggestions.value = [
      'Ê≥®ÊÑè‰øùÊåÅÊ≠•Ê≥ïÁöÑÁ®≥ÂÆöÊÄß',
      'Âá∫ÂâëÊó∂ÊâãËÖïÂäõÂ∫¶ÂèØ‰ª•Êõ¥Âä†ÈõÜ‰∏≠',
      'Âª∫ËÆÆÂ¢ûÂä†ÊüîÈüßÊÄßËÆ≠ÁªÉ'
    ]
  }

  analysisCompleted.value = true
}

// Â∑•ÂÖ∑ÂáΩÊï∞
const getCircleStyle = (score) => {
  return getCircleProgressStyle(score)
}

// Êìç‰ΩúÂáΩÊï∞
const viewAnalysisDetail = (id) => {
  uni.navigateTo({
    url: `/pages/analysis/detail?id=${id}`
  })
}

const shareResults = () => {
  try {
    // Ê£ÄÊü•ÂΩìÂâçÁéØÂ¢ÉÊòØÂê¶ÊîØÊåÅshowShareMenuÂäüËÉΩ
    if (typeof uni.showShareMenu === 'function') {
      uni.showShareMenu({
        withShareTicket: true,
        success: () => {
          console.log('ÂàÜ‰∫´ÊàêÂäü')
        },
        fail: (error) => {
          console.error('ÂàÜ‰∫´Â§±Ë¥•:', error)
          uni.showToast({
            title: 'ÂàÜ‰∫´ÂäüËÉΩÂºÄÂèë‰∏≠',
            icon: 'none'
          })
        }
      })
    } else {
      console.warn('ÂΩìÂâçÁéØÂ¢É‰∏çÊîØÊåÅshowShareMenuÂäüËÉΩ')
      uni.showToast({
        title: 'ÂΩìÂâçÁéØÂ¢É‰∏çÊîØÊåÅÂàÜ‰∫´ÂäüËÉΩ',
        icon: 'none'
      })
    }
  } catch (error) {
    console.error('Ë∞ÉÁî®ÂàÜ‰∫´ÂäüËÉΩÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'ÂàÜ‰∫´ÂäüËÉΩÂºÄÂèë‰∏≠',
      icon: 'none'
    })
  }
}

const downloadReport = async () => {
  try {
    uni.showLoading({ title: 'ÁîüÊàêÊä•Âëä‰∏≠...' })

    // ÁîüÊàêÊä•Âëä PDF ÊàñÂõæÁâá
    await new Promise(resolve => setTimeout(resolve, 2000))

    uni.hideLoading()
    uni.showToast({
      title: 'Êä•ÂëäÂ∑≤‰øùÂ≠òÂà∞Áõ∏ÂÜå',
      icon: 'success'
    })
  } catch (error) {
    uni.hideLoading()
    console.error('‰∏ãËΩΩÂ§±Ë¥•:', error)
    uni.showToast({
      title: '‰∏ãËΩΩÂ§±Ë¥•',
      icon: 'none'
    })
  }
}

const resetAnalysis = () => {
  analysisCompleted.value = false
  selectedFile.value = null
  sessionId.value = ''
  overallScore.value = 0
}
</script>

